	module.exports = {"data":{"site":{"siteMetadata":{"title":"Annalisa Valente"}},"contentfulPageSelectorPanel":{"pages":[{"name":"Brave the Shave","title":null,"slug":"/projects/brave-the-shave/","description":"Redesigning Brave the Shave microsite"},{"name":"Mindfulness Moments","title":"Mindfulness Moments","slug":"/projects/mindfulness-moments/","description":"Building a new fundraising proposition"},{"name":"Local Cancer Intelligence","title":"Local cancers intelligence","slug":"/projects/local-cancer-intelligence/","description":"New filter system for the LCI website"},{"name":"Google","title":"Google","slug":"/projects/google/","description":"Interaction designer contractor in the Google Play Console team"},{"name":"The Telegraph","title":"The Telegraph","slug":"/projects/the-telegraph/","description":"Working on various digital newspaper touch points"},{"name":"The Telegraph puzzle ","title":null,"slug":"/projects/the-telegraph-puzzle/","description":"Redesign the experience of the Telegraph puzzle website"},{"name":"Nectar ","title":"Nectar","slug":"/projects/nectar/","description":"  New way of discovering Nectar brands for web and iOS-Android app"},{"name":"MyMail & Nectar","title":"MyMail & Nectar","slug":"/projects/mymail-and-nectar/","description":"Launch of MyMail and Nectar proposition"},{"name":"Remember names","title":null,"slug":"/projects/face-name/","description":"Concept of an Android experience that helps to match faces to names"}]},"contentfulPage":{"slug":"/hackathon/","password":null,"panels":[{"__typename":"ContentfulHeaderPanel","text":"Side projects and hackathons"},{"__typename":"ContentfulContentPanel","content":{"content":"__Role:__ Interaction designer and strategiest \nProjects for inclusive design, accessibility and design for good\n\n\n__Autism Ideation & Hackathon - Microsoft React, Apr 2018__\n\nChallenge\n\nAutistic people often have trouble identifying and responding to other people’s emotions in a ‘neurotypical’ way. Their approach may be unconventional and appear one-sided. This reflects their difficulty with ‘cognitive empathy’ – the ability to read facial expressions, tone of voice and other non-verbal cues in the subtle, fast-moving and sophisticated way that is expected. They may appear insensitive or unusual as a result, despite often having a strong instinctive sense of how others are feeling (‘affective empathy’). Their neurotypical communication partners also find it hard to empathise with the autistic person’s experience and style of communication. This can lead to confusion and misunderstanding on both sides. This is known as the ‘double empathy’ problem. Therefore, how can we help autistic and non-autistic people to bridge their communication gaps?\n\nIdea\n\nWearables for kids that monitor potential triggers (e.g. anxiety, heart rate, blood flow, etc). If so then alerts educators, carers, parents of the  situation. Uses machine learning to understand and avoid triggers (e.g. volume colours on TV, teaching material, etc).\n\nThe notification to the person responsible for their safety will include tips and suggestions to help reduce the likelihood/severity of a meltdown. The AI can be trained to recognise patterns over an extended period of time. The data can be better used to anticipate when meltdowns arise, but also present data to medical professionals to better understand how and why they are affected.\n\n\n\n\n\n","childMarkdownRemark":{"html":"<p><strong>Role:</strong> Interaction designer and strategiest\nProjects for inclusive design, accessibility and design for good</p>\n<p><strong>Autism Ideation &#x26; Hackathon - Microsoft React, Apr 2018</strong></p>\n<p>Challenge</p>\n<p>Autistic people often have trouble identifying and responding to other people’s emotions in a ‘neurotypical’ way. Their approach may be unconventional and appear one-sided. This reflects their difficulty with ‘cognitive empathy’ – the ability to read facial expressions, tone of voice and other non-verbal cues in the subtle, fast-moving and sophisticated way that is expected. They may appear insensitive or unusual as a result, despite often having a strong instinctive sense of how others are feeling (‘affective empathy’). Their neurotypical communication partners also find it hard to empathise with the autistic person’s experience and style of communication. This can lead to confusion and misunderstanding on both sides. This is known as the ‘double empathy’ problem. Therefore, how can we help autistic and non-autistic people to bridge their communication gaps?</p>\n<p>Idea</p>\n<p>Wearables for kids that monitor potential triggers (e.g. anxiety, heart rate, blood flow, etc). If so then alerts educators, carers, parents of the  situation. Uses machine learning to understand and avoid triggers (e.g. volume colours on TV, teaching material, etc).</p>\n<p>The notification to the person responsible for their safety will include tips and suggestions to help reduce the likelihood/severity of a meltdown. The AI can be trained to recognise patterns over an extended period of time. The data can be better used to anticipate when meltdowns arise, but also present data to medical professionals to better understand how and why they are affected.</p>"}}},{"__typename":"ContentfulImagePanel","fullWidth":null,"media":{"description":"","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/4wCnORST56OOs8EKs0kEEq/be22dc88c7d942ba7888d2fccf3d008d/ezgif.com-video-to-gif__1_.gif"}}}]}},"pathContext":{"slug":"/hackathon/","p":"35675e68f4b5af7b995d9205ad0fc43842f16450"}}

